import interfaces from '../../utils/interface.js'

export default {
	data() {
		return {
			filter_by_list: [
				{
					text: '全部',
					selected: true,
					filter_by: 'all'
				},
				{
					text: '口碑',
					selected: false,
					filter_by: 'public'
				},
				{
					text: '热门',
					selected: false,
					filter_by: 'hot'
				}
			],
			goods_list: [], //商品列表数据
			filterby: 'all', //默认请求全部
			page: 1,
			size: 6,
			loadingText: '正在加载...'
		}
	},
	methods: {
		load_data() {
			this.$request({
				url: `${interfaces.getGoodsList}/${this.filterby}/${this.page}/${this.size}`,
				success: ((res) => {
					if(res.data.length > 0){
						res.data.forEach(item => { //push到里面 这样里面就会有全部的数据了
							this.goods_list.push(item);
						});
					}else{
						this.loadingText = "到底了";
					}
				})
			});
		},
		change_current_select(select_index) {
			this.filter_by_list.forEach((item,index) => {
				if(index == select_index){
					item.selected = true;
					//切换模块
					this.page = 1;
					this.loadingText = '加载中';
					this.goods_list = [];
					this.filterby = item.filter_by;
					this.load_data();
				}else{
					item.selected = false;
				}
			});
		},
		goods_detail(item) {
			uni.navigateTo({
				url: "./goods_detail?goodsInfo="+JSON.stringify(item)
			})
		}
	},
	onLoad(option) { //option 代表传的值
					
		//动态修改nav title
		uni.setNavigationBarTitle({
			title: option.name
		});
		
		//加载数据
		this.load_data();
	},
	onPullDownRefresh() { //下拉刷新方法 一定要在page.json中做一个配置
		setTimeout(() => {
			this.page = 1;
			this.loadingText = '加载中...';
			this.goods_list = [];
			this.load_data();
			uni.stopPullDownRefresh();
		},1000);
	},
	onReachBottom() { //上拉加载
		this.page ++;
		this.load_data();
	}
}
